<template>
  <view class="header">
    <view class="nav-left" v-if="goBack" @click="goBackDo">
      <text class="iconfont icon-left-arrow"></text>
    </view>
    <view class="header-tit ellipsis" v-if="headTitle">{{headTitle}}</view>
    <view class="nav-right">
      <slot name="right"></slot>
    </view>
  </view>
</template>

<script setup>
import { defineProps } from 'vue';

const props = defineProps({
  headTitle: {
    type: String,
    default: ''
  },
  goBack: {
    type: Boolean,
    default: false
  }
});

const goBackDo = () => {
  // 在UniApp中处理返回
  if (getCurrentPages().length > 1) {
    uni.navigateBack();
  } else {
    uni.switchTab({
      url: '/pages/index/index'
    });
  }
};
</script>

<style>
.header {
  width: 100%;
  position: relative;
  height: 96rpx;
  line-height: 96rpx;
  text-align: center;
  background: #ffffff;
}

.nav-left {
  position: absolute;
  bottom: 0;
  font-size: 28rpx;
  left: 32rpx;
}

.header-tit {
  max-width: 60%;
  margin: 0 auto;
  color: #323233;
  font-weight: 500;
  font-size: 40rpx;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.nav-right {
  position: absolute;
  bottom: 0;
  right: 32rpx;
}
</style> 